<template>
  <div class="overflow-x-hidden">
    <hero-section @scrollToElement="scrollToElement" ref="home"></hero-section>
    <about-section ref="about" id="about"></about-section>
    <approach-section></approach-section>
    <projects-section ref="projects" id="projects"></projects-section>
    <testimonial-section></testimonial-section>
    <footer-section @scrollToElement="scrollToElement"></footer-section>
    <arrow-top @scrollToElement="scrollToElement"></arrow-top>
  </div>
</template>

<script>
import HeroSection from "@/components/sections/HeroSection.vue";
import AboutSection from "@/components/sections/AboutSection.vue";
import ApproachSection from "@/components/sections/ApproachSection.vue";
import ProjectsSection from "@/components/sections/ProjectsSection.vue";
import TestimonialSection from "@/components/sections/TestimonialSection.vue";
import FooterSection from "@/components/sections/FooterSection.vue";
import ArrowTop from "@/components/partials/ArrowTop.vue";
export default {
  components: {
    HeroSection,
    AboutSection,
    ApproachSection,
    ProjectsSection,
    TestimonialSection,
    FooterSection,
    ArrowTop,
  },
  methods: {
    scrollToElement(el) {
      this.$refs[el].$el.scrollIntoView({ behavior: "smooth" });
    },
  },
};
</script>
