<template>
  <div class="bg-primary px-5 py-20 text-white works over">
    <div class="text-center w-auto md:w-10/12 lg:w-7/12 xl:w-1/2 mx-auto space-y-5" data-aos="zoom-in"
      data-aos-easing="ease-in-out" data-aos-mirror="true">
      <h4 class="text-gradient-2 orbitron text-3xl lg:text-5xl font-bold">Stellar Works</h4>
      <p class="text-xs md:text-sm  xl:text-sm 2xl:text-xl 3xl:text-base">Over the years, I've collaborated with both
        local and international companies to develop websites that are truly
        out of this world. Here are few that I consider to be the most challenging yet rewarding.</p>

    </div>
    <div class="grid lg:grid-cols-2 gap-4 mt-20" data-aos="zoom-out-up" data-aos-easing="ease-in-out">
      <div v-for="project in projects" :key="project.id"
        class="h-[300px] xl:h-[400px] 3xl:h-[600px] overflow-hidden relative">
        <div class="
                                              h-full
                                              bg-black bg-cover bg-center
                                              transition-transform
                                              duration-300
                                            " :style="background(project.featured_img)"
          :class="hover === project.id ? 'scale-150' : ''">
        </div>
        <div @mouseover="hover = project.id" @mouseleave="hover = null" class="overlay text-white p-5 flex items-end"
          :class="project.key != '' ? 'cursor-pointer' : ''" @click="viewProject(project.key)">
          <div class="text-left">
            <h3 class="text-xl md:text-2xl">{{ project.title }}</h3>
            <h5 class="text-[9px] sm:text-[10px] md:text-xs">
              {{ project.tagline }}
            </h5>
            <ul class="
                                                  flex flex-wrap
                                                  gap-3
                                                  uppercase
                                                  mt-4
                                                  text-[8px]
                                                  sm:text-[9px]
                                                  md:text-[10px]
                                                ">
              <li v-for="deliverable in project.deliverables" :key="deliverable"
                class="px-3 py-0.5 bg-zinc-800 rounded-full">
                {{ deliverable }}
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import projects from "@/assets/data/projects.json";
import router from "@/router";

export default {
  data() {
    return {
      projects,
      hover: false,
    };
  },
  methods: {
    background(src) {
      let bgImage = require("@/assets/projects/" + src);
      return {
        backgroundImage: `url("${bgImage}")`,
      };
    },
    viewProject(key) {
      router.push({ name: "project", params: { key: key } });
    },
  },
};
</script>