<template>
  <div>
    <div class="py-5 sm:py-10">
      <div @click="goHome()" class="uppercase orbitron tracking-[.25em] font-semibold text-center">
        Fornaleza
      </div>
      <div class="text-center">
        <nav class="space-x-5 mt-3 text-xs sm:text-base lg:text-xs xl:space-x-7 uppercase">
          <a href="/" class="nav" aria-label="Go to Home page">Home</a>
          <a href="/#about" class="nav" aria-label="Go to About section">About</a>
          <a href="/#projects" class="nav" aria-label="Go to Projects section">Projects</a>
          <a href="/contact" class="nav" aria-label="Go to Contact page">Contact</a>
        </nav>
      </div>

      <div class="mt-10 w-full h-[400px] bg-black bg-cover bg-center bg-fixed lg:h-[600px]"
        :style="background(project.featured_img)"></div>

      <div class="px-5 py-10 mx-auto lg:px-0 lg:w-8/12 lg:py-20 3xl:w-1/2">
        <project-details :project="project"></project-details>

        <project-challenge v-if="project.challenges" :challenges="project.challenges"></project-challenge>
        <project-solution v-if="project.solution" :img="project.solution"></project-solution>
        <!-- <project-wireframe v-if="project.brand_identity" title="Brand Identity" :src="project.brand_identity.src"
          :description="project.brand_identity.description">
        </project-wireframe>
        <project-wireframe v-if="project.project_wireframe" title="Project Wireframe" :src="project.project_wireframe.src"
          :description="project.project_wireframe.description">
        </project-wireframe> -->

        <!-- <div
        class="mt-10 w-full h-[400px] bg-black bg-cover bg-center lg:h-[600px]"
        :style="background(project.featured_img)"
      ></div> -->
      </div>
    </div>

    <footer-section></footer-section>
  </div>
</template>
<script>
import projects from "@/assets/data/projects.json";
import ProjectDetails from "@/components/partials/ProjectDetails.vue";
import FooterSection from "@/components/sections/FooterSection.vue";

import router from "@/router";

import ProjectChallenge from "@/components/partials/ProjectChallenge.vue";
import ProjectSolution from "@/components/partials/ProjectSolution.vue";
// import ProjectWireframe from "@/components/partials/ProjectWireframe.vue";

export default {
  data() {
    return {
      key: null,
      project: null,
      projects,
    };
  },
  components: {
    ProjectDetails,
    FooterSection,

    ProjectChallenge,
    ProjectSolution,
    // ProjectWireframe
  },
  created() {
    var param = this.$route.params.key;
    this.key = param;

    this.getProject();
  },
  methods: {
    getProject() {
      var project = projects.filter((projects) => projects.key.match(this.key));
      this.project = project[0];
    },
    background(src) {
      let bgImage = require("@/assets/projects/" + src);
      return {
        backgroundImage: `url("${bgImage}")`,
      };
    },
    goHome() {
      router.push("/");
    },
  },
};
</script>