      </div>
      <div class="h-full">
        <div class="h-[50px] flex items-center mt-[100px] lg:mt-[150px]">
          <p class="uppercase tracking-wider ">launching to the stars</p>
        </div>
        <div class=" pt-10 lg:pt-20 flex  items-center">
          <div class="space-y-10 text-sm lg:text-base font-light">
