</template>
<script>
export default {
  data() {
    return {
      countdown: 60
    }

  },
  methods: {
    countdownTimer() {
      if (this.countdown > 0) {
        setTimeout(() => {
          this.countdown -= 1
          this.countdownTimer()
        }, 1000)
      }
    }
  },
  created() {
    this.countdownTimer()
  }
}
</script>